/* ------------------------------------------------------------ *
	#reset
* ------------------------------------------------------------ */
* { margin: 0; padding: 0; outline: 0; }

*::before,
*::after { box-sizing: border-box; }

*::-moz-focus-inner { border: 0; padding: 0; }

*::selection { background-color: var(--color-primary); color: #fff; }

html { min-height: 100vh; width: 100%; font-size: var(--font-size-base); tap-highlight-color: rgba(0, 0, 0, 0); }
body { overflow-y: scroll; min-height: 100vh; width: 100%; font-family: var(--font-primary); font-size: var(--font-size-primary); line-height: 1.3; color: var(--color-text); -moz-osx-font-smoothing: grayscale; font-smoothing: antialiased; text-size-adjust: none; -webkit-text-size-adjust: none; }
body.fancybox-active { overflow-y: scroll; }

a { color: var(--color-link); text-decoration: none; cursor: pointer; }
a:hover { text-decoration: underline; }

img,
iframe,
video,
audio,
object { max-width: 100%; }

article,
aside,
details,
figcaption,
figure,
picture,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary { display: block; }

img,
iframe { border: 0 none; }

img { height: auto; display: inline-block; vertical-align: top; transition: all 0.3s; user-select: none; }

table { border-collapse: collapse; border-spacing: 0; }

label:hover,
input[type="radio"]:hover,
input[type="checkbox"]:hover { cursor: pointer; }
textarea { overflow: auto; resize: none; overflow-scrolling: touch; }

button,
input,
select,
textarea { font-family: var(--font-primary); transition: all 0.3s; }

small { font-size: 80%; }
sub,
sup { position: relative; /*vertical-align: baseline;*/ font-size: 75%; line-height: 0; }

mark { background-color: transparent; color: var(--color-secondary); }

h1,
h2,
h3,
h4,
h5,
h6 { line-height: 1.2; color: var(--color-titles); }

h1 { font-size: 42px; }
h2 { font-size: 30px; }
h3 { font-size: 26px; }
h4 { font-size: 22px; }
h5 { font-size: 20px; }
h6 { font-size: 18px; }

/* ------------------------------------------------------------ *\
	#fonts
\* ------------------------------------------------------------ */

@font-face {
    font-family: 'Rotondac';
    src: url('../../fonts/rotondac.otf') format('truetype');
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'Rotondac';
    src: url('../../fonts/rotondac-bold.otf') format('truetype');
    font-weight: 700;
    font-style: normal;
}

/* ------------------------------------------------------------ *
	#general
* ------------------------------------------------------------ */
.clearfix::after { content: ' '; display: table; clear: both; }

.container { background-image: url(../../images/background.png); }

.error-404 { background: radial-gradient(#fff, var(--color-primary)); width: 100%; height: 100vh; border-top: 10px solid var(--color-secondary); }

.logo { display: block; user-select: none; }
.logo img { max-width: 100%; height: auto; }

.map-embedded { position: relative; padding-bottom: 100%; background-color: var(--color-primary); }
.map-embedded iframe { position: absolute; top: 0; left: 0; width: 100%; height: 100%; }

.video-embedded { position: relative; padding-bottom: 56.23%; background-color: var(--color-primary); }
.video-embedded iframe { position: absolute; top: 0; left: 0; width: 100%; height: 100%; }

.date.news-date { display: block; padding-bottom: 15px; font-size: 18px; line-height: 18px; color: var(--color-neutral-dk); }

.title-default { font-size: 40px; line-height: 1; }


/* z-index hierarchy */
.header,
.footer,
.main,
.accents { position: relative; width: 100%; }

.header { z-index: 40; }
.footer { z-index: 30; }
.main { z-index: 20; }

.accents { z-index: 10; }

hr { display: block; border: none; width: 100%; height: 1px; margin: 10px 0; background-color: var(--color-neutral-dk); }

/* modals */

/* ------------------------------------------------------------ *
    #animations
* ------------------------------------------------------------ */
@keyframes slide-down {
	0% {
		opacity: 0;
		visibility: hidden;
	}
	100% {
		opacity: 1;
		visibility: visible;
	}
}

 @-webkit-keyframes slide-top-bottom {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    25% {
        -webkit-transform: translateY(-25px);
                transform: translateY(-25px);
    }
    50% {
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
    }
    75% {
        -webkit-transform: translateY(-25px);
                transform: translateY(-25px);
    }
    100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    }
  }

  @keyframes slide-top-bottom {
    0% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    25% {
        -webkit-transform: translateY(-25px);
                transform: translateY(-25px);
    }
    50% {
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
    }
    75% {
        -webkit-transform: translateY(-25px);
                transform: translateY(-25px);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }
  
  @-webkit-keyframes slide-top-bottom-reverse {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    25% {
        -webkit-transform: translateY(25px);
                transform: translateY(25px);
    }
    50% {
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }
    75% {
        -webkit-transform: translateY(25px);
                transform: translateY(25px);
    }
    100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    }
  }

  @keyframes slide-top-bottom-reverse {
    0% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    25% {
        -webkit-transform: translateY(25px);
                transform: translateY(25px);
    }
    50% {
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }
    75% {
        -webkit-transform: translateY(25px);
                transform: translateY(25px);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }

/* ------------------------------------------------------------ *
    #messages
* ------------------------------------------------------------ */
.msg { padding: 20px; margin: 5px 0 25px; background-color: var(--color-primary); background-repeat: no-repeat; background-position: 20px 20px; font-size: var(--font-size-primary); line-height: 1.3; color: #fff; }
.msg .title { margin-bottom: 10px; font-size: 18px; font-weight: bold; }

.msg-success,
.msg-error,
.msg-info,
.msg-warning { padding-left: 60px; }
.msg-success { background-color: var(--success); background-image: url("data:image/png;base64,") }
.msg-error { background-color: var(--error); background-image: url("data:image/png;base64,") }
.msg-info { background-color: var(--info); background-image: url("data:image/png;base64,") }
.msg-warning { background-color: var(--warning); background-image: url("data:image/png;base64,") }

p.msg { padding: 10px; background-image: none; }

/* ------------------------------------------------------------ *\
	#notifications
\* ------------------------------------------------------------ */
.toast-success { background-color: var(--success); }
.toast-error { background-color: var(--error); }
.toast-info { background-color: var(--info); }
.toast-warning { background-color: var(--warning); }

#toast-container > div { box-shadow: none; opacity: 1; }
#toast-container > div:hover { box-shadow: none; opacity: .9; }
.toast-progress { opacity: .6; height: 3px; background-color: #fff; }

/* ------------------------------------------------------------ *
	#grid
* ------------------------------------------------------------ */
.cols::before,
.cols::after { content: ' '; display: table; clear: both; }
.cols,
.col { box-sizing: border-box; }

.cols { margin-right: -20px; margin-left: -20px; padding-right: 20px; padding-left: 20px; }
.col { min-height: 10px; float: left; padding: 0 20px; }
.cols-full { padding-right: 0; padding-left: 0; }
.cols-no-gutter { margin-right: 0; margin-left: 0; }
.cols-no-gutter .col { padding: 0; }
.cols-full.cols-no-gutter { padding-right: 0; padding-left: 0; }
/*
class = "cols row" can be used so the children's height can be unified
:EXAMPLE
<div class="cols cols-full row">
    <div class="col col3of12">
        <div class="content"></div>
    </div>
    <div class="col col3of12">
        <div class="content"></div>
    </div>
    <div class="col col3of12">
        <div class="content"></div>
    </div>
    <div class="col col3of12">
        <div class="content"></div>
    </div>
</div>
.colntent { height: 100%; }
will make the height of all columns the same.

If this comment sounds dumb to you, try it without the row class :);
 */
.col1of12 { width: 8.33333333%; }
.col2of12 { width: 16.66666667%; }
.col20custom { width: 20%; }
.col3of12 { width: 25%; }
.col4of12 { width: 33.33333333%; }
.col5of12 { width: 41.66666667%; }
.col6of12 { width: 50%; }
.col7of12 { width: 58.33333333%; }
.col8of12 { width: 66.66666667%; }
.col9of12 { width: 75%; }
.col10of12 { width: 83.33333333%; }
.col11of12 { width: 91.66666667%; }
.col12of12 { width: 100%; }