/* ------------------------------------------------------------ *\
	#containers
\* ------------------------------------------------------------ */
.container { position: relative; z-index: 1; min-height: 100vh; width: 100%; overflow: hidden; }
.wrapper { max-width: 100%; width: var(--wrapper-width); margin: 0 auto; }
.main { background-color: #ffffff; }
.main.index-dots-bg { background-image: url('../../images/Dots_transparent@2x.png'); background-repeat: repeat; background-size: 200px 200px;}
body.no-scroll { overflow-y: hidden; }
/* ------------------------------------------------------------ *\
	#header
\* ------------------------------------------------------------ */

/* header-top */
.header-top { background: var(--color-bg); }
.nav-dundio-logo { width: 200px; height: 50px; fill: var(--color-primary); }

/* header-bottom */
.header-bottom { background: #ffffff; }

/* ------------------------------------------------------------ *\
	#footer
\* ------------------------------------------------------------ */

.footer-container { padding-top: 190px; position: relative; background: linear-gradient(to top, var(--color-tertiary) 50%, #ffffff); }
.footer-container .cloud-left-icon { width: 1835px; height: 610px; position: absolute; bottom: -5%; left: -10%; opacity: 0.85; }
.footer-container .cloud-right-icon { width: 1034px; height: 619px; position: absolute; bottom: -5%; right: -40%; opacity: 0.75;}
.footer-container .cloud-center-icon { width: 1920px; height: 533px; position: absolute; bottom: -5%; right: 0%; opacity: 0.93;}

/* footer newsletter */
.footer-newsletter { position: relative; text-align: center; margin-bottom: 140px; z-index: 40; }
.footer-newsletter .newsletter-heading { width: 700px; margin: 0 auto 30px; text-align: center; }
.footer-newsletter .title { margin-bottom: 30px; font-family: var(--font-primary); font-weight: 700; line-height: 1.5; font-size: 24px; text-transform: uppercase; color: #282929; }
.footer-newsletter .short-description { font-family: var(--font-primary); font-weight: 400; line-height: 1.5; font-size: 18px; color: #282929; }
.footer-newsletter .email-container { position: relative; }
.footer-newsletter .email-container .plane-icon { width: 1016px; height: 429px; }
.footer-newsletter .email-container form { width: 650px; position: absolute; top: 15%; left: 50%; transform: translate(-50%, -15%); }
.footer-newsletter .email-container form > .form-row > input { width: 445px; margin-right: 30px; padding: 10px; display: inline-block; vertical-align: middle; border: none; border-radius: 10px; background: #ffffff; }
.footer-newsletter .email-container form > .form-row > input.has-error { border: 1px solid #f31c4e; color: #f31b2a; }
.footer-newsletter .email-container form > .form-row > input.has-error::placeholder { color: #f31b2a; }
.footer-newsletter .email-container form > .form-row > input,
.footer-newsletter .email-container form > .form-row > input::placeholder { font-family: var(--font-primary); font-weight: 700; line-height: 1.2; font-size: 20px; color: #bbbbbb; }
.footer-newsletter .email-container form > .form-row .btn-primary { display: inline-block; vertical-align: middle; }

/* footer middle */
.footer-middle { position: relative; z-index: 40; margin-bottom: 10px; }
.footer-middle .title { height: 3.6em; font-family: var(--font-primary); font-weight: 700; line-height: 1.2; font-size: 16px; text-transform: uppercase; color: #377cee; }
.footer-middle ul { list-style: none; }
.footer-middle ul > li > a { font-family: var(--font-primary); font-weight: 400; line-height: 1.5; font-size: 16px; color: #858683; }
.footer-middle ul > .social-links { display: inline-block; vertical-align: top; }
.footer-middle ul > .social-links .facebook-icon,
.footer-middle ul > .social-links .instagram-icon { width: 27px; height: 27px; }


/* footer bottom */
.footer-bottom { padding: 40px 0; position: relative; z-index: 40; display: flex; flex-flow: nowrap; align-items: center; justify-content: space-between; }
.footer-bottom .copy-right-text {  font-family: var(--font-primary); font-weight: 400; line-height: 1.2; font-size: 10px; color: #4b4b4b; }
.footer-bottom .pytek-credit span { vertical-align: middle; font-family: var(--font-primary); font-weight: 400; line-height: 1.2; font-size: 10px; color: #4b4b4b; }
.footer-bottom .pytek-credit .pytek-icon { width: 55px; height: 31px; display: inline-block; vertical-align: middle; fill: #00b4f3; }

/* ------------------------------------------------------------ *\
	#pop-ups
\* ------------------------------------------------------------ */
.pop-ups-container { display: none; width: 100%; height: 100%; position: fixed; top: 0; z-index: 50; }
.popup-overlay { background: rgba(70, 70, 70, 0.57); width: inherit; height: inherit; position: relative; }

/* successfull registration pop-up */
.pop-up { display: none; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); }
.pop-up .popup-content { position: relative; width: 570px; height: auto; }
.pop-up .popup-content figure { width: fit-content; }
.popup-content .cloud-img { margin: 0 auto; }
.pop-up .bear-img { margin: -15px auto 0; text-align: center; }
.pop-up .text-content { position: absolute; top: 50%; left: 50%; text-align: center; width: fit-content; transform: translate(-50%, -50%); }
.pop-up .text-content .title { margin-bottom: 20px; font-family: var(--font-primary); font-weight: 700; line-height: 1.2; font-size: 18px; color: var(--color-primary); }
.pop-up .text-content .short-desc { display: block; margin-bottom: 25px; font-family: var(--font-primary); font-weight: 400; line-height: 1.2; font-size: 14px; color: #5a5a5a; }
.pop-up .btn-tertiary { margin: 0 auto; }
