/* ------------------------------------------------------------ *\
	#icons
\* ------------------------------------------------------------ */
.icon { display: inline-block; vertical-align: top; max-width: 100%; max-height: 100%; transition: all 0.3s; }

/* ------------------------------------------------------------ *\
	#btns
\* ------------------------------------------------------------ */
.btns { font-size: 0; line-height: 0; }

.btn { position: relative; box-sizing: border-box; display: block; max-width: fit-content; background-color: transparent; border: 0; cursor: pointer; appearance: none; transition: all 0.3s; }
.btn:hover { text-decoration: none; }

.btn-carousel { padding: 15px 55px; background: var(--color-secondary); font-family: var(--font-primary); font-weight: 700; line-height: 1.2; color: #072c4a; border: 1px dashed #ffffff; border-radius: 10px; }

.btn-primary { padding: 15px 40px; background: var(--color-tertiary); font-family: var(--font-primary); font-weight: 700; line-height: 1.2; font-size: 13px; color: #092f4c; border-radius: 10px; }

.btn-secondary { padding: 15px 40px; background: var(--color-secondary); font-family: var(--font-primary); font-weight: 700; line-height: 1.2; font-size: 13px; color: #092f4c; border-radius: 10px; }

.btn-tertiary { padding: 15px 40px; background: var(--color-primary); font-family: var(--font-primary); font-weight: 700; line-height: 1.2; font-size: 13px; color: #ffffff; border-radius: 10px; text-transform: uppercase; }

.btn-finish { min-width: 245px; padding: 15px 0; font-family: var(--font-primary); font-weight: 700; line-height: 1.2; font-size: 14px; color: #ffffff; text-align: center; background: var(--color-primary); border-radius: 10px; border: 1px solid transparent; }

.btn-continue { min-width: 245px; padding: 15px 0; font-family: var(--font-primary); font-weight: 700; line-height: 1.2; font-size: 14px; color: var(--color-primary); text-align: center; background: #ffffff; border-radius: 10px; border: 1px solid var(--color-primary); }

/* ------------------------------------------------------------ *\
	#article
\* ------------------------------------------------------------ */
.article h1,
.article h2,
.article h3,
.article h4,
.article h5,
.article h6 { margin-bottom: 10px; color: var(--color-titles); }

.article::after { content: ' '; display: table; clear: both; }

.article { font-size: var(--font-size-primary); line-height: 2.3rem; color: var(--color-text-article); }
.article * { max-width: 100%; }
.article hr { display: block; border: none; width: 100%; height: 1px; margin: 10px 0; background-color: #d9d9d9; }

.article p { padding-bottom: 10px; color: #000; font-size: 15px; line-height: 1; }
.article p + ul,
.article p + ol { margin-top: -10px; }
.article ul { list-style: none outside none; padding-bottom: 10px; }
.article ul	li { position: relative; padding: 0 0 5px 20px; color: #0b0b0b; font-size: 18px; line-height: 1;  }
.article ul	li::before { content: url(../../images/list-item.png); position: absolute; top: 0; left: 0; font-size: inherit; line-height: inherit; color: #7accc8; }
.article ul ul { padding-bottom: 0; padding-left: 25px; padding-top: 10px; }
.article ol { list-style-position: inside; padding-bottom: 10px; }
.article ol li { padding: 0 0 5px 0; }
.article blockquote { position: relative; z-index: 1; display: block; margin: 60px 0 40px; padding: 0 80px; font-size: 2.4rem; line-height: 30px; color: var(--color-text); }
.article blockquote::before { content: '"'; position: absolute; top: -76px; left: -23px; z-index: -1; font-size: 3rem; line-height: 1; color: #7accc8; }

.article table { margin-bottom: 10px; }

.article-centered { padding: 30px 0; text-align: center; }

.article-search ul { list-style: none; }

/* ------------------------------------------------------------ *\
	#breadcrumbs
\* ------------------------------------------------------------ */
.breadcrumbs ul li::before,
.breadcrumbs ul li::after,
.breadcrumbs ul li a,
.breadcrumbs ul li { transition: all 0.3s; }

.breadcrumbs { padding: 10px 0; display: inline-block; vertical-align: middle; }
.breadcrumbs ul li { display: inline; font-family: var(--font-primary); font-size: 16px; font-weight: 400; line-height: 1.2; color: #8b8b8c; }
.breadcrumbs ul li + li::before { content: '/'; margin-right: 5px; display: inline-block; vertical-align: middle; color: #8b8b8c; }
.breadcrumbs ul li a { color: #8b8b8c; text-decoration: none }
.breadcrumbs ul li:last-child a,
.breadcrumbs ul li a:hover { color: #3d3d3d; }

/* ------------------------------------------------------------ *\
	#paging
\* ------------------------------------------------------------ */
.paging { margin: 30px 0 70px; }
.paging li,
.paging a { transition: all 0.3s; }
.paging img{ padding-top: 1px; }

.paging { padding: 20px; text-align: right; }
.paging ul li { display: inline-block; vertical-align: middle; padding: 0 3px; font-size: 1.6rem; line-height: 22px; }
.paging ul li span,
.paging ul li a { position: relative; display: block; min-width: 22px; height: 22px; text-align: center; text-decoration: none; border-bottom: 1px solid transparent; background-color: transparent; }
.paging ul li a { color: #19191a; font-family: var(--font-primary); font-weight: 400; line-height: 1.2; font-size: 20px; }
.paging ul li span,
.paging ul .prev a,
.paging ul .next a { padding-bottom: 3px; background-color: transparent; color: #19191a; text-decoration: none; }
.paging ul .prev .arrow-left-icon,
.paging ul .next .arrow-right-icon { width: 10px; height: 10px; stroke: #19191a; }
.paging ul .prev .arrow-left-icon { transform: rotate(90deg); }
.paging ul .next .arrow-right-icon { transform: rotate(-90deg); }
.paging ul .selected a,
.paging ul li a:hover { color: var(--color-primary); border-bottom: 1px solid var(--color-primary); }
.paging .prev a:hover,
.paging .next a:hover { border-color: var(--color-primary); }


/* ------------------------------------------------------------ *\
	#selects
\* ------------------------------------------------------------ */
.nav-secondary .select-hidden { display: none; visibility: hidden; opacity: 0; }
.nav-secondary .select-head { display: inline-block; padding: 0px 10px; vertical-align: middle; position: relative; transition: all 0.3s ease-in; cursor: pointer; }
.nav-secondary .select-head .head-text { display: inline-block; font-size: 15px; }
.nav-secondary .select-head:active, .nav-secondary .select-head.active { display: inline-block; vertical-align: middle; visibility: visible; opacity: 1; }
.nav-secondary .select-options { position: absolute; visibility: hidden; opacity: 0; top: 100%; right: 0; left: 0; text-align: left; list-style: none; background-color: #fff; transform: scaleY(0); transform-origin: top; transition: all 0.4s; z-index: 999; }
.nav-secondary .select-options li { padding: 5px 10px; transition: all 0.3s; }
.nav-secondary .select-options.open { position: absolute; top: 100%; visibility: visible; opacity: 1; transform: scaleY(1); }
.nav-secondary .select-options.open li { padding: 10px; color: #3f3f3f; font-size: 15px; }
.nav-secondary .select-options.open li:hover { background-color: #ebebeb; }

.sorting-options-container { width: 100%; padding: 0 20px; box-sizing: border-box; text-align: right; position: absolute; top: 0; right: 20%; }
.sorting-options-container .order-by,
.sorting-options-container .to-show { position: relative; display: inline-block; vertical-align: middle;; }
.sorting-options-container .order-by .dropdown-icon,
.sorting-options-container .to-show .dropdown-icon { width: 8px; height: 5px; position: absolute; top: 50%; right: 10%; transform: translate(0, -50%); stroke: #6a6969; }
.sorting-options-container .select-hidden { display: none; visibility: hidden; opacity: 0; }
.sorting-options-container .select-head { display: inline-block; width: 100%; padding: 10px 50px; vertical-align: middle; position: relative; text-align: center; transition: all 0.3s ease-in; cursor: pointer; box-sizing: border-box; background: #ffffff; border-top-left-radius: 10px; border-top-right-radius: 10px; border-bottom-right-radius: 10px; border-bottom-left-radius: 10px; border: 1px solid #ebebeb; }
.sorting-options-container .select-head .head-text { display: inline-block; font-size: 15px; font-family: var(--font-primary); font-weight: 400; color: #6a6969; }
.sorting-options-container .select-head:active, .sorting-options-container .select-head.active { display: inline-block; vertical-align: middle; visibility: visible; opacity: 1; border-bottom-right-radius: 0px; border-bottom-left-radius: 0px; }
.sorting-options-container .select-options { position: absolute; visibility: hidden; opacity: 0; top: 100%; right: 0; left: 0; text-align: left; list-style: none; background-color: #fff; transform: scaleY(0); transform-origin: top; transition: all 0.4s; z-index: 999; border-bottom-left-radius: 10px; border: 1px solid #ebebeb; border-bottom-right-radius: 10px; }
.sorting-options-container .select-options li { padding: 5px 10px; transition: all 0.3s; }
.sorting-options-container .select-options.open {  top: 100%; visibility: visible; max-height: 200px; overflow: auto; opacity: 1; transform: scaleY(1);  }
.sorting-options-container .select-options.open li { padding: 10px; color: #3f3f3f; font-size: 15px; }
.sorting-options-container .select-options.open li:hover { background-color: #ebebeb; }

.add-quantity-container .select-hidden { display: none; visibility: hidden; opacity: 0; }
.add-quantity-container .select-head { display: inline-block; padding: 10px 50px; vertical-align: middle; position: relative; text-align: center; transition: all 0.3s ease-in; cursor: pointer; box-sizing: border-box; background: #ffffff; border-top-left-radius: 10px; border-top-right-radius: 10px; border-bottom-right-radius: 10px; border-bottom-left-radius: 10px; border: 1px solid #ebebeb; }
.add-quantity-container .select-head .head-text { display: inline-block; font-size: 15px; font-family: var(--font-primary); font-weight: 400; color: #6a6969; }
.add-quantity-container .select-head:active, .add-quantity-container .select-head.active { display: inline-block; vertical-align: middle; visibility: visible; opacity: 1; border-bottom-right-radius: 0px; border-bottom-left-radius: 0px; }
.add-quantity-container .select-options { position: absolute; visibility: hidden; opacity: 0; top: 100%; right: 0; left: 0; text-align: left; list-style: none; background-color: #fff; transform: scaleY(0); transform-origin: top; transition: all 0.4s; z-index: 999; border-bottom-left-radius: 10px; border: 1px solid #ebebeb; border-bottom-right-radius: 10px; }
.add-quantity-container .select-options li { padding: 5px 10px; transition: all 0.3s; }
.add-quantity-container .select-options.open {  top: 100%; visibility: visible; max-height: 200px; overflow: auto; opacity: 1; transform: scaleY(1);  }
.add-quantity-container .select-options.open li { padding: 10px; color: #3f3f3f; font-size: 15px; }
.add-quantity-container .select-options.open li:hover { background-color: #ebebeb; }
.add-quantity-container .select-options.open li a { color: #3f3f3f; font-size: 15px; }
.add-quantity-container .select-options.open li a:hover,
.add-quantity-container .select-options.open li a:focus { text-decoration: none; }

/* ------------------------------------------------------------ *\
	#socials
\* ------------------------------------------------------------ */
.socials { font-size: 0; line-height: 0; }

/* ------------------------------------------------------------ *\
	#tables
\* ------------------------------------------------------------ */
.table { width: 100%; border-collapse: collapse; border-spacing: 0; }
.table-wrap { overflow: auto; min-width: 600px; }

.table-basic { width: 100%; font-size: 2.3rem; line-height: 2.5rem; }
.table-basic th,
.table-basic td { border-width: 0 1px 1px; border-style: solid; border-color: var(--color-neutral-dk); padding: 5px; text-align: center; font-weight: normal; }
.table-basic th:first-child,
.table-basic td:first-child { border-left-color: transparent; }
.table-basic th:last-child,
.table-basic td:last-child { border-right-color: transparent; }

.table-grid,
.table-grid thead,
.table-grid tbody,
.table-grid tr,
.table-grid th,
.table-grid td { box-sizing: border-box; }
.table-grid th,
.table-grid td { vertical-align: top; }

/* ------------------------------------------------------------ *\
	#nav
\* ------------------------------------------------------------ */
nav a { transition: all 0.3s; }
nav a:hover { text-decoration: none; }
nav .level-1 { list-style: none; font-size: 0; line-height: 0; display: flex; justify-content: center; align-items: center; }

.nav-primary { width: fit-content; margin: 0 auto; }
.nav-primary a { display: block; }
.nav-primary .level-1 { position: relative; }
.nav-primary .level-1 > li { margin-right: 1px; display: flex; align-items: center; transition: .3s all ease-in; max-width: 130px; height: 60px;  padding: 10px 15px; cursor: pointer; text-align: center; box-sizing: border-box; border: 1px solid transparent; border-radius: 10px; background: linear-gradient( rgba(183, 122, 202, .9) , rgba(156, 104, 172, .9) 50%, rgba(183, 122, 202, .9) ); }
.nav-primary .level-1 > li:last-child { margin-right: 0px; }
.nav-primary .level-1 > li > a,
.nav-primary .level-1 > li > span { word-break: break-word; font-family: var(--font-primary); font-size: 16px; font-weight: 400; line-height: 1.2; color: var(--color-bg); text-decoration: none; }
.nav-primary .level-1 > li > a { display: none; }

.dropdown-menu { position: absolute; width: 100%; top: 100%; left: 0; transition: all .3s ease-in-out; visibility: hidden; opacity: 0; transform: scaleY(0); transform-origin: top; }
.level-1 > li > .dropdown-menu.has-flag { visibility: visible; opacity: 1; transform: scaleY(1); }
.dropdown-menu .heading { padding: 15px 0px 15px 30px; text-align: left; background: #f2f3f4; border-top-left-radius: 10px; border-top-right-radius: 10px; }
.dropdown-menu .heading .dropdown-heading-icon { width: 25px; height: 25px; margin-right: 30px; padding: 10px; display: inline-block; vertical-align: middle; background: #ffffff; box-shadow: 0 0 0 5px #ffffff; border: 1px dashed var(--color-primary); border-radius: 50%; }
.dropdown-menu .heading a { display: inline-block; vertical-align: middle; font-family: var(--font-primary); font-weight: 700; line-height: 1.2; font-size: 20px; color: var(--color-primary); }
.dropdown-menu .content { padding: 20px 10px; background: #ffffff; border-bottom-left-radius: 10px; border-bottom-right-radius: 10px; }
.dropdown-menu .content .level-2 { max-height: 450px; list-style: none; text-align: left; display: flex; flex-direction: column; flex-wrap: wrap; }
.dropdown-menu .content .level-2>li { text-align: left; margin-right: 5px; max-height: 100%; overflow: auto; scrollbar-width: none; }
.dropdown-menu .content .level-2>li::-webkit-scrollbar { width: 3px; }
.dropdown-menu .content .level-2>li::-webkit-scrollbar-track { background: none; }
.dropdown-menu .content .level-2>li::-webkit-scrollbar-thumb { background: var(--color-primary); border-radius: 60px; }
.dropdown-menu .content .level-2>li::-webkit-scrollbar-track-piece:start { background: transparent; margin-top: 15px; }
.dropdown-menu .content .level-2>li::-webkit-scrollbar-track-piece:end { background: transparent; margin-bottom: 15px; }
.dropdown-menu .content .level-2>li > a { display: inline-block; vertical-align: middle; margin-right: 5px; transition: all .3s ease-in-out; font-family: var(--font-primary); font-weight: 400; line-height: 2; font-size: 16px; color: #404040; }
.dropdown-menu .content .level-2>li:hover > a { color: var(--color-primary); }
.dropdown-menu .level-3 { padding-left: 10px; display: block; }
.dropdown-menu .level-3 > li .dropdown-icon { width: 8px; height: 5px; display: inline-block; vertical-align: middle; margin-right: 5px; transform: rotate(-90deg); stroke: var(--color-primary); }
.dropdown-menu .level-3 > li > a { display: inline-block; vertical-align: middle; transition: all .3s ease-in-out; font-family: var(--font-primary); font-weight: 400; line-height: 2; font-size: 16px; color: #7d7d7d; }
.dropdown-menu .level-3 > li:hover > a { color: var(--color-primary); }

.nav-secondary { max-width: 1300px; display: flex; justify-content: space-between; align-items: center; margin: 0 auto; }
.nav-secondary .hamburger { display: none; vertical-align: middle; }
.nav-secondary .hamburger span { display: block; margin-bottom: 5px; width: 20px; height: 2px; background: var(--color-primary); transition: all .3s ease-in-out;}
.nav-secondary .hamburger.has-flag span:first-child { transform: rotate(45deg) translateY(10px); }
.nav-secondary .hamburger.has-flag span:nth-child(2) { opacity: 0; visibility: hidden; }
.nav-secondary .hamburger.has-flag span:last-child { transform: rotate(-45deg) translateY(-10px); }
.nav-secondary figure { padding: 10px 0 10px 0; display: inline-block; vertical-align: middle; }
.nav-secondary .search-box { display: inline-block; vertical-align: middle; }
.nav-secondary .search-box form { border: 1px solid #e4e6e5; box-sizing: border-box; border-radius: 7px; }
.nav-secondary .search-box form input { width: 290px; height: 40px; padding-left: 20px; display: inline-block; vertical-align: middle; border: none; border-top-left-radius: 7px; border-bottom-left-radius: 7px; }
.nav-secondary .search-box form input::placeholder,
.nav-secondary .search-box form input { font-family: var(--font-primary); font-weight: 400; font-size: 16px; line-height: 1.2; color: #bbbbbb; }
.nav-secondary .search-box form button { height: 40px; background: #bae8ff; padding: 0 15px; display: inline-block; vertical-align: middle; border: none; border-top-right-radius: 7px; border-bottom-right-radius: 7px; }
.nav-secondary .search-box form .search-icon { width: 15px; height: 15px; fill: #000000;  }
.nav-secondary > ul { display: inline-block; vertical-align: middle; }
.nav-secondary > ul > li { position: relative; padding: 25px 20px; display: inline-block; vertical-align: middle; position: relative; border-right: 1px solid #f2f2f2; }
.nav-secondary > ul > li .text-icon { width: 17px; height: 17px; padding-right: 10px; fill: #949494; display: inline-block; vertical-align: middle; }
.nav-secondary > ul > li span { padding-right: 10px; display: inline-block; vertical-align: middle; font-family: var(--font-primary); font-weight: 400; font-size: 15px; line-height: 1.2; color: #949494; }
.nav-secondary > ul > li .dropdown-icon { width: 8px; height: 5px; fill: #949494; display: inline-block; vertical-align: middle; }
.nav-secondary > ul > li .shopping-bag-icon { padding: 0px 10px; width: 20px; height: 22px; fill: #949494; }
.nav-secondary > ul > li .cart-items-number { position: absolute; top: 20%; left: 60%; display: block; font-size: 8px; padding: 3px 5px; border-radius: 50%; color: #fff; background: red; }

.nav-profile { padding-right: 50px; }
.nav-profile .title { margin-bottom: 30px; font-family: var(--font-primary); font-weight: 700; line-height: 1.2; font-size: 20px; color: #243042; }
.nav-profile .profile-tab { margin-bottom: 20px; cursor: pointer; }
.nav-profile .profile-tab .tab-icon { float: left; width: 19px; height: 19px; margin-right: 10px; fill: #949494; transition: all .3s ease; }
.nav-profile .profile-tab span {float: left; font-family: var(--font-primary); font-weight: 400; line-height: 1.2; font-size: 16px; color: #949494; transition: all .3s ease; }
.nav-profile .profile-tab .tab-right-arrow { float: right; width: 8px; height: 5px; padding: 7px 0; transform: rotate(-90deg); stroke: #949494; transition: all .3s ease; }
.nav-profile .profile-tab.has-flag .tab-icon,
.nav-profile .profile-tab:hover .tab-icon { fill: var(--color-primary); }
.nav-profile .profile-tab.has-flag span,
.nav-profile .profile-tab:hover span { color: var(--color-primary); }
.nav-profile .profile-tab.has-flag .tab-right-arrow,
.nav-profile .profile-tab:hover .tab-right-arrow { stroke: var(--color-primary); }


/* ------------------------------------------------------------ *\
	#forms
\* ------------------------------------------------------------ */

/* form elements */
legend { display: table; float: left; margin: 0; padding: 0; width: 100%; }
legend + * { clear: both; }
fieldset { border: 0; padding: 0.01em 0 0 0; min-width: 0; }

fieldset,
legend,
.field input,
.field select,
.field textarea { appearance: none; box-sizing: border-box; }
.field select::-ms-expand { display: none; }
.field [type="search"]::-webkit-search-cancel-button,
.field [type="search"]::-webkit-search-decoration { -webkit-appearance: none; }
.field ::-webkit-file-upload-button { -webkit-appearance: button; font: inherit; }

.field input,
.field select,
.field textarea { max-width: 100%; width: inherit; font-size: 14px; line-height: 20px; padding: 5px;  box-shadow: 2px 2px 5px var(--color-neutral-dk); background-color: var(--color-neutral-lt); }

.field textarea { height: 160px; background-color: var(--color-neutral-lt); }

.field select[disabled],
.field textarea[disabled],
.field input[disabled],
.field input[disabled] { background-color: var(--color-neutral-dk); }

.choose { padding-bottom: 10px; line-height: 1; }
.choose::after { content: ' '; display: block; clear: both; }
.choose:last-child { padding-bottom: 0; }

.radio > input,
.check > input { float: left; width: 24px; height: 24px; margin: 0 10px 0 0; opacity: 0; }
.radio > label,
.check > label { font-size: 1.4rem; line-height: 24px; color: #555; user-select: none; }
.radio > label::before,
.check > label::before { content: ''; position: absolute; top: 0; left: 0; display: block; width: 24px; height: 24px; border: 1px solid var(--color-neutral-dk); background-color: #fff; transition: all 0.3s; pointer-events: none; }
.radio > label::after,
.check > label::after { content: ''; position: absolute; top: 0; left: 0; width: 24px; height: 24px; background-color: var(--color-primary); transition: all 0.3s; opacity: 0; visibility: hidden; pointer-events: none; }
.radio > input:focus + label::before,
.check > input:focus + label::before { border-color: #7accc8; }
.radio > input:checked + label::before,
.check > input:checked + label::before { background-color: #7accc8; }
.radio > input:checked + label::after,
.check > input:checked + label::after { opacity: 1; visibility: visible; }
.radio > label::before { border-radius: 50%; }
.radio > label::after { border-radius: 50%; }

/* form errors and messages */
.choose,
.field { position: relative; }
.field.error > textarea,
.field.error > select,
.field.error > input { position: relative; border-bottom-color: var(--error); color: var(--error); }
.field.error::after { content: '!'; position: absolute; top: 50%; left: 7px; font-size: 2rem; color: var(--error); font-weight: bold; transform: translateY(-50%); }
.choose.error > label { color: var(--error); }
.field.success > textarea,
.field.success > select,
.field.success > input { position: relative; border-bottom-color: var(--success); color: var(--success); }
.field.success::after { content: ''; position: absolute; top: 50%; left: 0; width: 20px; height: 20px; margin-top: 2px; color: var(--success); transform: translateY(-50%); text-align: center; }
.choose.success > label { color: var(--success); }
.form-msg { padding: 10px 0; font-size: 1.4rem; line-height: 2rem; color: var(--color-text); }
.form-msg.error { color: var(--error); }
.form-msg.success { color: var(--success); }
.form-hint { padding: 3px 0; font-size: 1.4rem; line-height: 2rem; color: var(--color-text); }

/* form types */
.related-target { display: none; }
.related-target.has-flag { display: block; }

.form.hide { display: none; }
.form .form-title { margin-bottom: 20px; text-align: center; }
.form .col .btn { display: block; min-width: 0; width: 100%; }
.form .label,
.form .form-label { display: block; padding: 10px; margin-bottom: 10px; font-size: 1.6rem; line-height: 2rem; }
.form .form-label.required::after { content: '*'; }
.form hr { display: block; border: 0; height: 1px; margin: 15px 0; background-color: var(--color-neutral-dk); }
.form .form-row { padding-bottom: 20px; overflow: hidden; }
.form .form-row + hr { margin-top: 0; }
.form .captcha { position: relative; max-width: 100%; width: 304px; height: 78px; }
.form .btn-inline { margin-top: 3rem; }
