.level2-container .filter-heading { width: 100%; padding: 10px 30px; vertical-align: middle; position: relative; text-align: center; transition: all 0.3s ease-in; font-size: 19px; font-family: var(--font-primary); font-weight: 400; color: #8b8b8c; cursor: pointer; box-sizing: border-box; border-top-left-radius: 10px; border-top-right-radius: 10px; border-bottom-right-radius: 10px; border-bottom-left-radius: 10px; border: 1px solid #ebebeb; }
.level2-container .filter-heading:active, .level2-container .filter-heading.active { visibility: visible; opacity: 1; border-bottom-right-radius: 0px; border-bottom-left-radius: 0px; }
.level2-container .filter-content-level2 { padding: 10px 0 10px 15px; position: absolute; visibility: hidden; opacity: 0; top: 100%; right: 0; left: 0; text-align: left; list-style: none; background-color: #fff; transform: scaleY(0); transform-origin: top; transition: all 0.4s; z-index: 999; border-bottom-left-radius: 10px; border: 1px solid #ebebeb; border-bottom-right-radius: 10px; }
.level2-container .filter-content-level2.open { position: relative; display: block; top: 100%; visibility: visible; max-height: 200px; overflow: auto; opacity: 1; transform: scaleY(1);  }

/* google map holder */
#js_map { width: 100%; height: 610px; background: #f8f8f8; }